
$category-margin: 8px;

.category {
    min-width: calc((100% - 10.4 * $category-margin) / 4);
    height: 100%;
    // height: fit-content;
    background-color: #fff;
    box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
    margin: 0 $category-margin;
    border-radius: 5px;
    padding: 10px;
    position: relative;
    
    &:first-child {
        margin-left: calc($category-margin * 2.2);
    }
    
    &:last-child {
        margin-right: calc($category-margin * 2.2);
    }
    
    .category_heading {
        background-color: #fff;
        font-size: 1.8rem;
        line-height: 22px;
        font-weight: 440;
        text-align: left;
        padding: 8px 5px 14px 10px;
        display: flex;
        align-items: center;
        justify-content: space-between;

        .cat-delete-icon {
            font-size: 1.4rem;
            color: #999;
        }
    }
    
    .category_list {
        max-height: 536px;
        width: 100%;
        position: relative;
        overflow-y: auto;
    }
}